import './App.css';
import React,{ useState } from 'react';
import sound1 from './click.mp3';
import sound2 from './equal.mp3';
function App() {
  const [invalue,setinvalue]=useState('');
  const [darkmode,setdarkmode]= useState(false);
  const clicked=(e)=>{
    new Audio(sound1).play();
    setinvalue(invalue.concat(e.target.name));
  }
  const clear =()=>{
    new Audio(sound2).play();
    setinvalue("");
  }
  const backspace =()=>{
    new Audio(sound1).play();
    setinvalue(invalue.slice(0,invalue.length-1));
  }
  const Tanfun=()=>{
    new Audio(sound1).play();
    setinvalue( Math.tan(invalue).toFixed(4))
  }
  const Sinfun=()=>{
    new Audio(sound1).play();
    setinvalue( Math.sin(invalue).toFixed(4))
  }
  const Cosfun=()=>{
    new Audio(sound1).play();
    setinvalue(Math.cos(invalue).toFixed(4))
  }
  const lnfun=()=>{
    new Audio(sound1).play();
    setinvalue(Math.log(invalue).toFixed(4));
  }
  const logfun=()=>{
    new Audio(sound1).play();
    setinvalue(Math.log10(invalue).toFixed(4));
  }
  const Square=()=>{
    new Audio(sound1).play();
    setinvalue(Math.pow(invalue,2));
  }
  const sqrtfun =()=>{ 
    new Audio(sound1).play();  
    setinvalue(Math.pow(invalue,0.5).toFixed(4));
  }
  const equals =()=>{
    try{
      new Audio(sound2).play();
      setinvalue(eval(invalue).toString());
    }
    catch(err){
      setinvalue('ERROR');
    }
  }
  return (<div className={darkmode? "dark":"light"}>
<div class="container">
<div class="calculator">
    <div class="inputbox">
        <input type="text" name ="cal_input" id="cal_input" placeholder="calculate the numbers"
         value={invalue}
          onChange={(e)=>{
         setinvalue(e.target.value);
        }} />
    </div>
    <div class="display">
       <div class="row1">
            <button onClick={clear} className='highlight'>AC</button>
            <button onClick={clicked} name='%'>%</button>
            <button onClick={logfun}>log 10</button>
            <button onClick={clicked} name='/'>/</button>
            <button className="highlight" onClick={backspace}>Del</button>
        </div>
        <div class="row2">
            <button className="highlight" onClick={Sinfun}>sin</button>
            <button onClick={clicked} name='7'>7</button>
            <button onClick={clicked} name='8'>8</button>
            <button onClick={clicked} name='9'>9</button>
            <button onClick={clicked} name='+'>+</button>
        </div>
        <div class="row3">
            <button className="highlight" onClick={Cosfun}>cos</button>
            <button onClick={clicked} name='4'>4</button>
            <button onClick={clicked} name='5'>5</button>
            <button onClick={clicked} name='6'>6</button>
            <button onClick={clicked} name='-'>-</button>
        </div>
        <div class="row4">
            <button className="highlight" onClick={Tanfun}>tan</button>
            <button onClick={clicked} name='1'>1</button>
            <button onClick={clicked} name='2'>2</button>
            <button onClick={clicked} name='3'>3</button>
            <button onClick={clicked} name='*'>*</button>
        </div>
        <div class="row5">
            <button className="highlight" onClick={Square}>square</button>
            <button onClick={sqrtfun}>√</button>
            <button onClick={clicked} name='0'>0</button>
            <button onClick={clicked} name='.'>.</button>
            <button className="highlight" onClick={equals}>=</button>
        </div>
    </div>
</div>
</div>
    </div>
    
  );
}

export default App;
